

.app {
  width: 100vh;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  background-color: #282c34;
  color: white;
  padding-left: 20px;
  /* padding: 1rem 2rem; */
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}


/* Make sure the header is always fixed */
.header2 {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  background-color: #fff;
  padding: 10px 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: top 0.3s ease;
}

/* Adjust the layout when keyboard is visible */
.keyboard-visible main {
  margin-top: 60px;  /* This ensures the content doesn't get hidden behind the header */
}

/* You can add extra styling to the header when the keyboard is visible */
.keyboard-visible .header2 {
  top: 0;  /* Ensures the header remains at the top */
}

